import React from 'react';
import Form from 'react-bootstrap/Form';
import Card from 'react-bootstrap/Card';
import Button from 'react-bootstrap/Button';

class EmbedComponent extends React.Component {
    
    static defaultProps = {
        embId: -1,
        delFunc: (id) => {return;}
    }

    render() {
        return (
            <Form.Group>
            <Card>
                <Card.Body>
                    <Button variant="outline-dark" onClick={this.props.delFunc(this.props.embId)}>x</Button>
                    <Form.Control type="text" placeholder="Author name" name={"embed_username" + this.props.embId}/>
                    <Form.Control type="text" placeholder="Embed title" name={"embed_title" + this.props.embId}/>                </Card.Body>
            </Card>
            </Form.Group>
        );
    }
}

export default EmbedComponent;